import React from 'react';
import SkewedContainer from 'sc-react';

import ExpSection from './expSection';
import EduSection from './eduSection';
import jobExp from './jobExp';
import eduExp from './eduExp';

import Grid from '@material-ui/core/Grid';
import Icon from '@material-ui/core/Icon';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import ScrollAnimation from 'react-animate-on-scroll';

const Resume = () => (
	<SkewedContainer
		top="right"
		bottom="right"
		bgColor="white"
		className="resume"
		noMargin
	>
		<Grid container justify="center">
			<ScrollAnimation animateOnce animateIn="bounceInDown">
				<Typography variant="display2" className="text-center">
					R&#233;sum&#233;
				</Typography>
			</ScrollAnimation>
		</Grid>
		<Grid container justify="center">
			<ScrollAnimation animateOnce animateIn="bounceInDown">
				<Button
					variant="flat"
					href="https://docs.google.com/document/d/1sSrzCyaX1cOhHndrKpnWjS5U9VkeYyx-MiNzq2Ej_VI/edit?usp=sharing"
				>
					<Icon>get_app</Icon>
					Download
				</Button>
			</ScrollAnimation>
		</Grid>
		<Divider style={{ margin: '5vh 15vw', width: '70vw' }} />
		<Grid container justify="center">
			<Grid item lg={2} style={{ padding: 20 }}>
				<ScrollAnimation animateOnce animateIn="bounceInLeft">
					<Typography variant="display1">Experience</Typography>
				</ScrollAnimation>
			</Grid>
			<Grid item lg={8} style={{ padding: 20 }}>
				<ScrollAnimation animateOnce animateIn="bounceInRight">
					{jobExp.map(job => {
						return (
							<ExpSection
								key={job.start}
								title={job.title}
								iconType="work"
								description={job.description
									.split('\n')
									.map((i, idx) => {
										return (
											<div key={idx}>
												{i}
												<br />
											</div>
										);
									})}
								company={job.company}
								start={job.start}
								end={job.end}
							/>
						);
					})}
					<Divider />
				</ScrollAnimation>
			</Grid>
		</Grid>
		<Grid container justify="center">
			<Grid item lg={2} style={{ padding: 20 }}>
				<ScrollAnimation animateOnce animateIn="bounceInLeft">
					<Typography variant="display1">Education</Typography>
				</ScrollAnimation>
			</Grid>
			<Grid item lg={8} style={{ padding: 20 }}>
				<ScrollAnimation animateOnce animateIn="bounceInRight">
					{eduExp.map(school => {
						return (
							<EduSection
								key={school.start}
								title={school.title}
								description={school.description
									.split('\n')
									.map((i, idx) => {
										return <span key={idx}>{i}</span>;
									})}
								academics={school.academics}
								company={school.name}
								awards={school.awards}
								activities={school.activities}
								start={school.start}
								end={school.end}
							/>
						);
					})}
				</ScrollAnimation>
			</Grid>
		</Grid>
		<br />
		<br />
		<br />
		<br />
		<br />
	</SkewedContainer>
);

export default Resume;
